	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/css-selection-styles/index.md absPath of file >>> MarkdownRemark","html":"<p>Did you know you could customize the selection colours of html pages with CSS?  I didn't - until now.  Turns out, there's a <a href=\"http://www.w3.org/TR/css3-selectors/#UIfragments\" title=\"Open link in a new window\">CSS3 pseudo-element called ::selection</a>.  Naturally, this isn't yet a technique supported by browsers like Internet Explorer, but it is supported in [albeit with different syntax] by Firefox and Safari.  Interesting, eh?  Check an article on <a href=\"http://www.quirksmode.org/css/selection.html\" title=\"Open link in a new window\">CSS Selection Styles from Quirksmode</a>, and have a look at some of the creative effects you can get with it <a href=\"http://metaatem.net/selcolor.php\" title=\"Open link in a new window\">here</a>.</p>","frontmatter":{"title":"CSS Selection Styles?","date":"February 24, 2008","tags":["css"],"url":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/creating-an-icon-with-photoshop/","tags":["open-source","photoshop","photoshop-resources","resources"],"title":"Creating an Icon with Photoshop","image":null,"date":"2007-07-21T09:29:25.000Z"}},"next":{"frontmatter":{"path":"/blog/creating-custom-chromes-for-air-applications-in-flex-flash/","tags":["flash","flex","air"],"title":"Creating custom chromes for AIR applications in Flex & Flash","image":null,"date":"2009-01-15T16:30:01.000Z"}}}}

